* {box-sizing:border-box;}

body {margin:0;padding:0;background:#eff3f7;height:100%;
    font-family:-apple-system,BlinkMacSystemFont,'Segoe UI',Roboto,Oxygen,Ubuntu,Cantarell,'Open Sans','Helvetica Neue',sans-serif;}

html, body, #root, .container {height:100%;display:flex;justify-content:center;}

.container .loader {width:100%;height:100vh;display:flex;justify-content:center;align-items:center;font-weight:300;}
.container .movies {display:grid;grid-template-columns:repeat(2, minmax(400px, 1fr));grid-gap:100px;padding:50px;width:80%;padding-top:70px;}

@media screen and (max-width: 1090px) {
    .movies {grid-template-columns:1fr;width:100%;}
}