.container .movies .movie {background:#fff;color:#adaeb9;margin-bottom:70px;font-weight:300;border-radius:5px;padding:20px;box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);}
.container .movies .movie a {display:flex;align-items:flex-start;justify-content:space-between;text-decoration:none;color:inherit;}
.container .movies .movie img {position:relative;top:-50px;width:100%;max-width:150px;margin-right:30px;box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);}
.container .movies .movie .movie__data .movie__title {font-weight:300;margin:0 0 5px 0;font-size:24px;color:#2c2c2c;}
.container .movies .movie .movie__data .movie__year {font-weight:300;margin:0;} 
.container .movies .movie .movie__data .movie__genres {list-style:none;padding:0;margin:5px 0;display:flex;}
.container .movies .movie .movie__data .movie__genres li {margin-right:10px;font-size:14px;}